import router from "./index";
import { Route } from "vue-router";
import store from "@/store";

export interface RoleNavigation {
  name: string;
  navigate: () => Promise<Route>;
}

function createRepoRoleNavigation(name: string, additionalParamsGenerator?: (storeState: any) => { [key: string]: string }): RoleNavigation {
  return {
    name,
    navigate() {
      const { repoUrl } = router.currentRoute.params;
      return router.push({ name, params: { repoUrl, ...additionalParamsGenerator?.(store.state) } });
    },
  };
}

const repositoryListButton = createRepoRoleNavigation("Repository List");
const pullRequestsButton = createRepoRoleNavigation("Pull Requests");
const reviewerTrackingButton = createRepoRoleNavigation("Reviewer Tracking");
const reviewsButton = createRepoRoleNavigation("Reviews");
const configurationsButton = createRepoRoleNavigation("Configurations");
const performanceReportButton = createRepoRoleNavigation("Performance Report", (store) => ({ username: store.user.login }));

export const roleNavigations = {
  admin: [repositoryListButton, pullRequestsButton, reviewerTrackingButton, reviewsButton, configurationsButton],
  author: [repositoryListButton, pullRequestsButton],
  reviewer: [repositoryListButton, pullRequestsButton, performanceReportButton],
};
