<template>
  <div class="main">
    <Sidebar class="sidebar">
      <div slot="buttons" class="px-3 mt-5">
        <slot name="buttons"></slot>
      </div>
    </Sidebar>
    <v-main class="main-container">
      <div class="my-6 mx-10">
        <div class="d-flex align-center mb-8" style="gap: 20px">
          <div class="icon" @click="$router.go(-1)">
            <v-icon color="white" large>mdi-arrow-left</v-icon>
          </div>
          <span class="title">
            <span>{{ pageTitle }}</span>
            <v-tooltip nudge-left="10px" left v-if="!!currentPageHelp">
              <template v-slot:activator="{ attrs, on }">
                <span v-bind="attrs" v-on="on" class="helpButton px-4"
                  >Help
                  <v-icon size="26" color="black" class="mb-1">mdi-help-box</v-icon>
                </span>
              </template>
              <span v-html="formattedTooltipText(currentPageHelp.helpText)"></span>
            </v-tooltip>
          </span>
        </div>
        <slot name="prepend"></slot>
        <div class="content mt-8">
          <slot></slot>
        </div>
      </div>
    </v-main>
  </div>
</template>

<script>
import Sidebar from "./Sidebar.vue";
import { mapGetters } from "vuex";
export default {
  components: { Sidebar },
  props: {
    pageTitle: {
      type: String,
      default: "No Title",
    },
  },
  computed: {
    ...mapGetters(["repoConfig"]),
    currentPageHelp() {
      return this.helpTexts.find((o) => o.page === this.$router.currentRoute.name);
    },
    helpTexts() {
      return [
        {
          page: "NewPullRequest",
          helpText: `1. Click on any line highlighted green to edit it and add a manual bug.\n
                     2. Confirm manual bug addition by pressing the tick.\n
                     3. Choose a reviewer to assign this pull request to them for review.\n
                     4. Press the Confirm button when done.
                     &#8277; Green lines represent added lines, red lines represent removed lines.`,
        },
        {
          page: "PullRequestDetails",
          helpText: `&#8277; You can visit the files changed tab to view the files that were\n changed and leave comments on them.
                     &#8277; The pull request can be merged if the latest review is a successful\n approving review and there are no conflicts.
                     &#8277; In code diff, green lines represent added lines, red lines represent\n removed lines.`,
        },
        {
          page: "AddReview",
          helpText: `&#8277; Hover your cursor on any line highlighted green and then press the bug\n icon to mark a line as buggy.\n
                     &#8277; You can also leave comments for the author by hovering your cursor\n over any line and pressing the + icon.\n
                     &#8277; The accuracy of your review will be calculated as the number of injected\n bugs marked divided by the number of total injected bugs.\n
                     &#8277; The exact line containing the injected bug must be marked for it to be\n counted towards the review accuracy score.\n
                     &#8277; The review accuracy must be at least ${this.repoConfig.faultyThreshold}% for it to be considered\n a successful review.
                     &#8277; Green lines represent added lines, red lines represent removed lines.`,
        },
        {
          page: "PullRequestConfirmation",
          helpText: `&#8277; The code is shown here without the manually injected bugs.\n
                     &#8277; You can leave comments for the author by hovering your cursor\n over any line and pressing the + icon.
                     &#8277; Green lines represent added lines, red lines represent removed lines.`,
        },
        {
          page: "Reviewer Tracking",
          helpText: `You may interact with the black chips in the data table for more\n information.`,
        },
        {
          page: "Reviews",
          helpText: `Click on a data table row to view details of the review.`,
        },
      ];
    },
  },
};
</script>

<style scoped lang="scss">
.icon {
  border-radius: 50%;
  background: #22252d;
  height: 40px;
  width: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.content {
  background: #0d1117;
  border: 1px solid #cccccc;
  box-sizing: border-box;
  border-radius: 10px;
}
.main {
  display: flex;
  &-container {
    margin-left: 15.4%;
    max-width: 84.6% !important;
  }
}
.sidebar {
  width: 18.4%;
  height: 100vh;
}
.title {
  font-family: Poppins;
  font-style: normal;
  font-weight: 1000;
  font-size: 33px !important;
  line-height: 49px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: #ffffff;
  width: 100%;
}
.helpButton {
  font-size: 20px;
  border-radius: 10px;
  cursor: pointer;
  background: white;
  color: black;
}
</style>
