






























import { mapActions, mapGetters } from "vuex";
import { RoleNavigation, roleNavigations } from "@/router/roleNavigations";
import Vue from "vue";
import UserIcon from "@/components/UserIcon.vue";

export default Vue.extend({
  components: { UserIcon },
  data() {
    return {};
  },
  mounted() {
    this.getRepoConfig({ repoUrl: this.$route.params.repoUrl });
  },
  methods: {
    ...mapActions(["logout", "getRepoConfig"]),
    routeMatch(route: string) {
      return this.$route.name === route;
    },
    logoutUsingStore() {
      this.logout();
    },
  },
  computed: {
    ...mapGetters(["role", "user", "repoConfig"]),
    roleButtons(): RoleNavigation[] {
      if (this.role === "reviewer" && !this.repoConfig.seeSelfPerformance) {
        return roleNavigations.reviewer.filter((button) => button.name !== "Performance Report");
      }
      return roleNavigations[this.role as keyof typeof roleNavigations];
    },
  },
});
